html, body {
  height: 100vh;
  width: 100vw;
}

label {
  font-weight: bold;
}

.calendar-screen {
  display: flex;
  flex-flow: column;
  height: 100vh;
}

.rbc-calendar {
  height: 100%;
}


/* Modal */

.ReactModalPortal > div {
  opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
  align-items: center;
  display: flex;
  justify-content: center;
  transition: opacity .2s ease-in-out;
  z-index: 999;
}

.modal-fondo {
  background-color: rgba(0, 0, 0, 0.3);
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  position: fixed;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
  opacity: 0;
}


.modal {
  background: white;
  border-radius: 5px;
  color: rgb(51, 51, 51);
  outline: none;
  padding: 10px;
  display: block;
  width: 600px;
  height: auto;
}


.btn-add {
  border-radius: 80%;
  bottom: 25px;
  font-size: 30px;
  padding: 25px;
  position: fixed;
  right: 25px;
}

.btn-delete {
  bottom: 25px;
  padding: 10px;
  position: fixed;
  left: 40px;
}